@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed');

.Container{
    position: relative;
}

.App {
  color: lightgray;
  font-family: 'Roboto Condensed', sans-serif;
  position: absolute;
    background-color: transparent;
}

.gradientBackground {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(rgb(83, 159, 231), purple);
  overflow:hidden;
  position: fixed;
  z-index: -1;
}

.overlayLanguageSwitch{
  background-color: transparent;
  overflow:hidden;
  position: fixed;
  z-index: 1;
  right: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ProjectSliderSection {
  display: flex;
  justify-content: center;
}

.IntroductionText{
  width:100%;
}