.ProjectSliderView {
    display: flex;           
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 200vh;
}

.ProjectSliderTitle {
    width: 1000px;
}

#ProjectSlider {
    border-radius: 20px;
    border: 3px solid lightgray;
    width: 1000px;
    height: 600px;
    background-color: rgba(211, 211, 211, 0.3);
    /*padding: 20px;*/
}

#sliderContent {
    /*display: flex;*/
}

.ProjectDetails{
    display: flex;
    padding: 40px 40px 0px 40px;
}

.sliderButtonLeft {
    float: left;
    background: none;
    border: none;
}

.sliderButtonLeft:hover {
    transform: scale(1.2);
}

.sliderButtonRight {
    float: right;
    background: none;
    border: none;
}

.sliderButtonRight:hover {
    transform: scale(1.2);
}

.ProjectImageDiv {
    height: 500px;
    width: 300px;
    display: flex;   
    justify-content: center; 
}
.ProjectImage {
    height: 100%;
}

.projectNavigations {
    display: flex;
    justify-content: space-between;
    height: 60px;
    padding: 0px 40px 0px 40px;
}

.ProjectInformation {
    flex: 1;
    padding-left: 20px;
}

.ProjectTitle {
    margin: 0px;
}